.register__auth-error-message {
  display: block;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 15px;
  color: #FF0000;
  box-sizing: border-box;
  position: absolute;
  width: inherit;
  margin-top: 10px;
}

@media all and (min-width: 300px) and (max-width: 550px) {
  .register__auth-error-message {
    margin-top: 20px;
  }
}
